/*.modal*/
	.modal-content {
		padding: 2rem;
		border: none;
		border-radius: 10px;
	}

	.modal-header {
		justify-content: center;
		/*padding: 0 1rem;*/

		border: none;
	}

	.modal-body {
	}

	.modal-footer {
		justify-content: center;
		padding: 0 1rem;

		border: none;
	}

	.btn-close {
	  position: absolute;
    top: 1rem;
    right: 1rem;
    opacity: 0.2;

    z-index: 1;
	}

  #modal-video .btn-close {
    padding: 15px;
    background-color: rgba(185, 185, 185, 0.2);
    border-radius: 50%;
    filter: invert(1);
    opacity: 1;

    &:hover {
      background-color: rgba(185, 185, 185, 0.4);
    }
    &:focus {
      box-shadow: none;
      background-color: rgba(185, 185, 185, 0.6);
    }
  }

  #modal-video .modal-fullscreen .video-footer {
    display: none;
  }

  #modal-video .modal-fullscreen .video-player {
    max-height: 100vh;
  }

	@media (min-width: 576px) {
	  #modal-first-video .modal-dialog {
	    max-width: 900px;
	  }

		#modal-user-type .modal-dialog {
	    max-width: 630px;
		}

		#modal-login .modal-dialog {
			max-width: 380px;
		}

		#modal-video .modal-dialog:not(.modal-fullscreen) {
      margin: 40px;
		}
		#modal-video .modal-dialog {
			max-width: 100%;
		}
	}

	@media (max-width: 576px) {
    .modal-dialog {
      margin: 0;
    }
    .modal-content {
      min-height: 100vh;
      justify-content: center;
		  padding: 1rem;

		  border-radius: 0px;
    }
    .modal-body {
      flex-grow: 0;
    }
		#modal-video .modal-content {
		  justify-content: flex-start;
		}
	}